import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Pagination from "@mui/material/Pagination";

const dispatchedEvents = [];

const SrsMuiPagination = function (props, ref) {
  return (
    <Pagination
      count={props.count}
      page={props.page}
      onChange={props.onChange}
      color={props.color}
      size={props.size}
      shape={props.shape}
      variant={props.variant}
      disabled={props.disabled}
      hidePrevButton={props.hidePrevButton}
      hideNextButton={props.hideNextButton}
      showFirstButton={props.showFirstButton}
      showLastButton={props.showLastButton}
      siblingCount={props.siblingCount}
      boundaryCount={props.boundaryCount}
      renderItem={props.renderItem}
      theme={props.muiTheme}
    />
  );
};

const styles = `
  /* Custom styles for the MuiPagination component */
`;

const props = [
  {
    name: "count",
    defaultValue: 1,
    type: "number",
  },
  {
    name: "page",
    defaultValue: 1,
    type: "number",
  },
  {
    name: "onChange",
    defaultValue: null,
    type: "function",
  },
  {
    name: "color",
    defaultValue: "primary",
    type: "string",
  },
  {
    name: "size",
    defaultValue: "medium",
    type: "string",
  },
  {
    name: "shape",
    defaultValue: "round",
    type: "string",
  },
  {
    name: "variant",
    defaultValue: "text",
    type: "string",
  },
  {
    name: "disabled",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "hidePrevButton",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "hideNextButton",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "showFirstButton",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "showLastButton",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "siblingCount",
    defaultValue: 1,
    type: "number",
  },
  {
    name: "boundaryCount",
    defaultValue: 1,
    type: "number",
  },
  {
    name: "renderItem",
    defaultValue: null,
    type: "function",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiPagination),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
